$navbar-highlight: rgba(1, 255, 255, 0.795);

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.navbar {
  background: #19191a;
  height: 80px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  position: sticky;
  top: 0;
  z-index: 999;
}

.navbar-container {
  display: flex;
  justify-content: space-between;
  height: 80px;
  &.container {
    z-index: 1;
    width: 100%;
    //max-width: 1300px;
    margin-right: auto;
    margin-left: auto;
    padding-right: 50px;
    padding-left: 50px;
  }
}

.navbar-logo {
  color: #fff;
  justify-self: start;
  text-decoration: none;
  font-size: 1.4rem;
  display: flex;
  align-items: center;

  .navbar-logo-img {
    height: 38px;
    margin-right: 30px;
    filter: drop-shadow(0px 0px 10px aqua);

    //filter: drop-shadow(3px 3px 4px red) sepia(100%) drop-shadow(-3px -3px 4px blue);
  }
  @media (prefers-reduced-motion: no-preference) {
    .navbar-logo-img {
      animation: navbar-logo-spin infinite 20s linear;
    }
  }
  @keyframes navbar-logo-spin {
    from {
      transform: rotate(0deg);
    }
    to {
      transform: rotate(360deg);
    }
  }
}

.navbar-icon {
  margin-right: 0.5rem;
}

.nav-menu {
  display: flex;
  align-items: center;
  justify-content: center;
  list-style: none;
  text-align: center;
  //justify-content: flex-end;
}

.nav-item {
  height: 80px;
  border-bottom: 2px solid transparent;
  &:hover {
    border-bottom: 2px solid $navbar-highlight;
  }
}

.nav-links {
  color: #fff;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: 0.5rem 1rem;
  height: 100%;
  &.activated {
    color: $navbar-highlight;
  }
}

.fa-times,
.fa-bars {
  color: #fff;
}

.menu-icon {
  display: none;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }

  .nav-menu {
    display: flex;
    flex-direction: column;
    width: 100%;
    position: absolute;
    top: 80px;
    left: -100%;
    opacity: 1;
    transition: all 0.5s ease;
    &.active {
      background: #19191a;
      left: 0;
      opacity: 1;
      transition: all 0.6s ease;
      z-index: 1;
    }
  }

  .nav-links {
    text-align: center;
    padding: 2rem;
    width: 100%;
    display: table;
    &:hover {
      color: #09bef0;
      transform: scale(1.2);
      transition: all 0.3s ease;
    }
  }

  .nav-item {
    width: 100%;
    &:hover {
      border: none;
    }
  }

  .navbar-logo {
    position: relative;
    top: 0;
    left: 0;
    //position: absolute;
    //transform: translate(25%, 50%);
  }

  .menu-icon {
    display: block;
    position: absolute;
    top: 0;
    right: 0;
    transform: translate(-100%, 60%);
    font-size: 1.8rem;
    cursor: pointer;
  }
}
